.main {
  height: 100% !important;
  min-height: 100vh;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 210px 0 60px;
  color: #FFF1DA;
  transition: background-image 1.5s ease-in-out;
  &__title {
    max-width: 1250px;
    text-align: center;
    font-family: 'Vetrino';
    font-size: 80px;
    line-height: 100%;
    font-weight: 400;
    letter-spacing: 0.8px;
    margin-bottom: 40px;
    span {
      color: #DD7500;
    }
  }
  &__btn {
    height: 54px;
    text-align: start;
    gap: 10px;
    padding: 5px 12px 5px 78px;
    margin: 0 auto 100px auto;
    .btn-icon {
      position: absolute;
      z-index: 2;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      max-width: 65px;
      img {
        max-width: 100%;
      }
    }
    .btn-text {
      font-size: 10px;
    }
    .btn-play {
      position: relative;
      z-index: 2;
      width: 30px;
      height: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #FFF1DA;
      border-radius: 50%;
      padding: 10px;
      img {
        max-width: 100%;
      }
    }
  }
  &__items {
    max-width: 830px;
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 90px / 3));
    gap: 45px;
    padding: 0 30px;
    margin: 0 auto;
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    &-number {
      font-family: 'Vetrino';
      font-size: 20px;
      line-height: 100%;
      letter-spacing: -0.8px;
      margin-bottom: 15px;
      span {
        font-size: 56px;
        letter-spacing: 2.24px;
      }
    }
    &-text {
      font-size: 10px;
      line-height: 140%;
      letter-spacing: 1px;
      text-transform: uppercase;
    }
  }
  &__mob {
    display: none;
  }
}
.area {
  position: relative;
  z-index: 2;
  height: 100vh !important;
  background-image: url("../img/area-img.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 425px 0 50px 0;
  .container {
    height: 100%;
  }
  &__inner {
    height: 100%;
  }
  &__desc {
    position: relative;
    z-index: 2;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 15px;
  }
  &__number {
    flex-shrink: 0;
    font-family: 'Vetrino';
    font-size: 360px;
    line-height: 60%;
    letter-spacing: -14.4px;
    span {
      font-size: 60px;
      line-height: 100%;
      letter-spacing: -1.2px;
    }
  }
  &__title {
    text-align: right;
    font-family: 'Vetrino';
    font-size: 60px;
    line-height: 100%;
    letter-spacing: -1.2px;
  }
}
.advantages {
  position: relative;
  padding: 150px 0 75px;
  .container {
    position: relative;
    z-index: 2;
  }
  .section-top {
    max-width: 566px;
  }
  &__img {
    position: absolute;
    top: -400px;
    right: 0;
    pointer-events: none;
    img {
      max-width: 100%;
    }
  }
  &__items {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: stretch;
    margin-top: 100px;
  }
  &__item {
    position: relative;
    transform: translateY(-60px);
    min-height: 240px;
    width: 50%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 24px;
    border-top: 1px solid rgba(255, 241, 218, 0.2);
    border-bottom: 1px solid rgba(255, 241, 218, 0.2);
    padding-top: 15px;
    padding-bottom: 15px;
    margin-top: -1px;
    opacity: 0;
    &:before,
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      transform: translate(0, -50%);
      height: calc(100% - 50px);
      background: rgba(255, 241, 218, 0.2);
    }
    &:nth-child(1) {
      padding-right: 50px;
      margin-top: 0;
      &:after {
        right: 0;
        width: 1px;
      }
    }
    &:nth-child(2) {
      padding-left: 50px;
      padding-right: 50px;
      margin-top: 0;
      &:before {
        left: 0;
        width: 1px;
      }
    }
    &:nth-child(3) {
      width: 100%;
      max-width: 710px;
      padding-left: 50px;
      padding-right: 50px;
      &:before {
        left: 0;
        width: 1px;
      }
      &:after {
        right: 0;
        width: 1px;
      }
    }
    &:nth-child(4) {
      border-bottom: unset;
      padding-left: 145px;
      padding-right: 50px;
      &:before {
        left: 75px;
        width: 1px;
      }
      &:after {
        right: 0;
        width: 1px;
      }
    }
    &:nth-child(5) {
      border-bottom: unset;
      padding-left: 50px;
      padding-right: 50px;
      &:before {
        left: 0;
        width: 1px;
      }
    }
    &-number {
      font-family: 'Vetrino';
      font-size: 100px;
      line-height: 80%;
      letter-spacing: -4px;
      margin-bottom: 24px;
      span {
        font-size: 30px;
        line-height: 100%;
        letter-spacing: -1.2px;
      }
    }
    &-title {
      max-width: 175px;
      font-size: 13px;
      line-height: 125%;
      letter-spacing: 1.3px;
      text-transform: uppercase;
    }
    &-group2 {
      display: flex;
      align-items: center;
      gap: 20px;
    }
    &-icon {
      width: 40px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    &-text {
      max-width: 200px;
      font-size: 16px;
      line-height: 125%;
      font-weight: 300;
      letter-spacing: -0.32px;
      color: rgba(255, 241, 218, 0.6);
    }
  }
}
.map {
  min-height: 100vh;
  padding: 75px 0;
  .section-top {
    max-width: 660px;
    margin-bottom: 50px;
    &__title {
      margin-bottom: 20px;
    }
    &__subtitle {
      margin: 0;
    }
  }
}
.house {
  position: relative;
  padding: 75px 0 600px;
  margin-bottom: 75px;
  overflow: hidden;
  .section-top {
    position: relative;
    z-index: 2;
    max-width: 650px;
    margin-bottom: 75px;
  }
  &__img {
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 0);
    max-width: 1320px;
    width: calc(100% - 60px);
    height: 600px;
    transition: all 1s;
    img {
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: 100%;
      height: 100%;
      border-radius: 16px;
    }
  }
  &__text {
    position: absolute;
    z-index: 2;
    left: 0;
    bottom: 0;
    transform: translateY(-30px);
    max-width: 500px;
    padding: 60px;
    font-size: 24px;
    line-height: 125%;
    font-weight: 300;
    letter-spacing: -0.48px;
    opacity: 0;
    pointer-events: none;
    transition: all 1.5s;
  }
}
.house--active {
  .section-top {
    color: #000;
  }
  .house__img {
    width: 100%;
    max-width: 100%;
    height: 100%;
  }
  .house__text {
    transform: translateY(0);
    opacity: 1;
    pointer-events: auto;
  }
}
.services {
  padding: 75px 0;
  .section-top {
    max-width: 940px;
    margin-bottom: 100px;
  }
  &__wrap {
    position: relative;
    max-width: 1190px;
    &:hover {
      .swiper-button-prev,
      .swiper-button-next {
        display: flex;
      }
    }
  }
  &__slider {
    max-width: 600px;
  }
  .swiper {
    overflow: unset;
  }
  .swiper-slide {
    transform: translateY(60px);
    max-width: 600px;
    width: 100% !important;
    opacity: 0;
    transition: all 0.125s;
    .service-card {
      max-width: 400px;
    }
    .service-card__desc {
      display: none;
    }
    &-active {
      .service-card {
        max-width: 100%;
        margin: 0;
      }
      .service-card__desc {
        display: block;
      }
    }
    &-next {
      .service-card {
        max-width: 500px;
        margin: 0;
      }
      .service-card__desc {
        display: block;
      }
      .service-card__price {
        display: none;
      }
      .service-card__btn {
        display: none;
      }
    }
    &-prev {
      .service-card {
        max-width: 500px;
        margin-left: auto;
      }
      .service-card__desc {
        display: block;
      }
      .service-card__price {
        display: none;
      }
      .service-card__btn {
        display: none;
      }
    }
    &-next + .swiper-slide {
      .service-card {
        margin-left: -100px;
      }
    }
    &-next + .swiper-slide + .swiper-slide {
      .service-card {
        margin-left: -300px;
      }
    }
    &-next + .swiper-slide + .swiper-slide ~ .swiper-slide {
      .service-card {
        margin-left: -500px;
      }
    }
    &-next + .swiper-slide + .swiper-slide ~ .swiper-slide + .swiper-slide {
      .service-card {
        margin-left: -700px;
      }
    }
    &-next + .swiper-slide + .swiper-slide ~ .swiper-slide + .swiper-slide + .swiper-slide {
      .service-card {
        margin-left: -900px;
      }
    }
    &-next + .swiper-slide + .swiper-slide ~ .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide {
      .service-card {
        margin-left: -1100px;
      }
    }
    &-next + .swiper-slide + .swiper-slide ~ .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide {
      .service-card {
        margin-left: -1300px;
      }
    }
  }
  .swiper-slide:has(+ .swiper-slide-prev) {
    .service-card {
      margin-left: auto;
      margin-right: -100px;
    }
  }
  .swiper-slide:has(+ .swiper-slide + .swiper-slide-prev) {
    .service-card {
      margin-left: auto;
      margin-right: -300px;
    }
  }
  .swiper-slide:has(+ .swiper-slide + .swiper-slide + .swiper-slide-prev) {
    .service-card {
      margin-left: auto;
      margin-right: -500px;
    }
  }
  .swiper-slide:has(+ .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide-prev) {
    .service-card {
      margin-left: auto;
      margin-right: -700px;
    }
  }
  .swiper-slide:has(+ .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide-prev) {
    .service-card {
      margin-left: auto;
      margin-right: -900px;
    }
  }
  .swiper-slide:has(+ .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide-prev) {
    .service-card {
      margin-left: auto;
      margin-right: -1100px;
    }
  }
  .swiper-slide:has(+ .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide-prev) {
    .service-card {
      margin-left: auto;
      margin-right: -1300px;
    }
  }
  .swiper-button-prev,
  .swiper-button-next {
    top: 40%;
    width: 120px;
    height: 120px;
    display: none;
    justify-content: center;
    align-items: center;
    background: #FFF1DA;
    border-radius: 50%;
    opacity: 1 !important;
    &:after {
      display: none;
    }
    svg {
      max-width: 80%;
      max-height: 100%;
    }
  }
  .swiper-button-prev {
    left: -25px;
  }
  .swiper-button-next {
    right: -25px;
  }
  .swiper-button-disabled {
    display: none !important;
  }
}
.service-card {
  background: #fff;
  border-radius: 28px;
  &:hover {
    .service-card__img {
      img {
        transform: scale(1.125);
      }
    }
  }
  &__img {
    position: relative;
    border-radius: 16px;
    padding-bottom: 66%;
    overflow: hidden;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 16px;
      transition: all 0.5s;
    }
  }
  &__desc {
    padding: 24px 20px 20px 20px;
  }
  &__format {
    font-size: 13px;
    line-height: 100%;
    letter-spacing: 1.3px;
    text-transform: uppercase;
    color: #1B1D25;
  }
  &__items {
    display: flex;
    align-items: start;
    gap: 40px;
    margin-top: 16px;
  }
  &__item {
    position: relative;
    &:not(:last-child) {
      padding-right: 40px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: 0;
        transform: translate(0, -50%);
        width: 1px;
        height: calc(100% - 20px);
        background: rgba(27, 29, 37, 0.1);
        pointer-events: none;
      }
    }
    &-number {
      font-family: 'Vetrino';
      font-size: 60px;
      line-height: 100%;
      letter-spacing: -0.6px;
      color: #1B1D25;
      span {
        line-height: 100%;
        letter-spacing: -0.42px;
      }
    }
    &-text {
      font-size: 12px;
      line-height: 100%;
      letter-spacing: 0.72px;
      color: #1B1D25;
      margin-top: 5px;
    }
  }
  &__price {
    font-family: 'Vetrino';
    font-size: 42px;
    line-height: 100%;
    letter-spacing: -0.42px;
    color: #DD7500;
    margin-top: 60px;
    span {
      font-size: 60px;
      line-height: 100%;
      letter-spacing: -0.6px;
    }
  }
  &__btn {
    width: 100%;
    height: 44px;
    margin-top: 24px;
    span {
      font-size: 12px;
      letter-spacing: 1.2px;
    }
  }
}
.area2 {
  padding-top: 75px;
  &__slider {
    position: relative;
    &:hover {
      .swiper-button-prev,
      .swiper-button-next {
        display: block;
      }
    }
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    position: relative;
    height: auto;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 60px 0 280px;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(-2.73deg, rgba(0, 0, 0, 0) 51.426%,rgba(0, 0, 0, 0.3) 98.484%);
      pointer-events: none;
      transition: all 0.5s;
    }
    &:hover {
      &:after {
        background: linear-gradient(-2.73deg, rgba(0, 0, 0, 0) -26.294%,rgba(0, 0, 0, 0.3) 20.764%);
      }
    }
    &__inner {
      position: relative;
      z-index: 2;
    }
  }
  &__title {
    font-family: 'Vetrino';
    font-size: 60px;
    letter-spacing: -0.6px;
  }
  &__subtitle {
    font-size: 20px;
    line-height: 125%;
    letter-spacing: -0.5px;
    margin-top: 40px;
  }
  .swiper-button-prev,
  .swiper-button-next {
    width: 80px;
    display: none;
    opacity: 1 !important;
    &:after {
      display: none;
    }
    svg {
      width: 100%;
      height: auto;
    }
  }
  .swiper-button-prev {
    left: 60px;
  }
  .swiper-button-next {
    right: 60px;
  }
  .swiper-button-disabled {
    display: none !important;
  }
}

@media screen and (max-width: 1300px) {
