        font-size: 24px;
        letter-spacing: 0.24px;
      }
      &-text {
        font-size: 10px;
        letter-spacing: 0.1px;
      }
    }
    &__price {
      font-size: 18px;
      letter-spacing: -0.18px;
      margin-top: 32px;
      span {
        font-size: 24px;
        letter-spacing: 0.24px;
      }
    }
    &__btn {
      margin-top: 16px;
    }
  }
  .area2 {
    padding-top: 35px;
    .swiper-slide {
      padding: 25px 0 140px;
    }
    &__title {
      font-size: 24px;
      letter-spacing: 0.24px;
    }
    &__subtitle {
      font-size: 14px;
      letter-spacing: 0.14px;
      margin-top: 16px;
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: block !important;
    }
  }
}

@media screen and (max-width: 640px) {
  .advantages {
    .container {
      padding: 0;
    }
    .section-top {
      padding: 0 20px;
    }
    &__overflow {
      padding: 0 20px;
      overflow-x: auto;
      &::-webkit-scrollbar {
        height: 0;
      }
    }
    &__items {
      width: 600px;
      justify-content: start;
    }
    &__item {
      &:nth-child(3) {
        max-width: 440px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    &__mob-group {
      &--1 {
        background-image: url("../img/main-img--1.webp");
      }
      &--2 {
        background-image: url("../img/main-img2--1.webp");
      }
      &--3 {
        background-image: url("../img/main-img3--1.webp");
      }
      &--4 {
        background-image: url("../img/main-img4--1.webp");
      }
    }
  }
  .house {
    padding-bottom: 200px;
    &__img {
      height: 200px;
    }
  }
}

@media screen and (max-width: 359px) {
  .area {
    &__desc {
      flex-direction: column;
      align-items: start;
    }
  }
}